#footer{
    background-color: var(--background-color);
    width: 100%;
    height: 120px;
    display: flex;
    align-items: center;
    position: relative;
    border-bottom: solid 5px var(--accent-color);
}
#footer > h2{
    margin-left: 50px;
    font-size: 30px;
}
#footer-socials{
    position: absolute;
    right: 50px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
}