#hero-section{
    width: 100%;
    height: 70vh;
    background-image: url('../../assets/background/hero.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}
#hero-section::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000;
    opacity: 0.6;
}
#hero-section > svg{
    z-index: 5;
    position: absolute;
    top: 30px;
    right: 50px;
    cursor: pointer;
    transition: transform 0.25s ease;
}
#hero-section > svg:hover{
    transform: scaleY(1.2);
}
#hero-section > h3{
    position: absolute;
    top: 10px;
    left: 50px;
    font-size: 30px;
    color: #FFFFFF;
    z-index: 5;
}
#hero-section > img{
    width: 20%;
    z-index: 5;
    border-radius: 100%;
}
#hero-section > a{
    z-index: 5;
    margin-top: 20px;
}
