body{
    margin: 0;
    padding: 0;
}
body::-webkit-scrollbar{
    width: 5px;
    background-color: transparent;
}
body::-webkit-scrollbar-thumb{
    background-color: var(--accent-color);
    border-radius: 100px;
}
:root{
    --background-color: #FFFFFF;
    --accent-color: #F44545;
}

@font-face{
    font-family: 'Nunito';
    src: url('../assets/fonts/Nunito-Regular.ttf');
    font-weight: normal;
}
@font-face{
    font-family: 'Nunito';
    src: url('../assets/fonts/Nunito-Bold.ttf');
    font-weight: bold;
}

h1, h2, h3 {
    font-family: 'Nunito';
    font-weight: bold;
}
p, a, input{
    font-family: 'Nunito';
    font-weight: normal;
}

/* Buttons */
.btn{
    padding: 15px 30px;
    background-color: var(--accent-color);
    color: var(--background-color);
    text-decoration: none;
    border-radius: 100px;
    transition: filter 0.5s ease;
}
.btn:hover{
    filter: brightness(1.5);
}