.section{
    display: grid;
    grid-template-columns: 30% 1fr;
    align-items: center;
    width: 100%;
    height: 70vh;
    position: relative;
}
.section.reverse{
    grid-template-columns: 1fr 30%;
}
.section-text{
    text-align: center;
    margin: 0 auto;
    width: 70%;
    grid-column: 1/2;
}
.section-text > h2{
    font-size: 40px;
}
.section-text > hr{
    border: none;
    width: 80px;
    height: 4px;
    background-color: var(--accent-color);
}
.section-text > p{
    margin-top: 40px;
}
.section-image{
    height: 100%;
    width: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
    grid-column: 2/-1;
}
.section-image::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: #000000;
    opacity: 0.4;
}
.section.reverse > .section-image{
    grid-column: 1/2;
    grid-row: 1;
}
.section.reverse > .section-text{
    grid-column: 2/-1;
    grid-row: 1;
}
.section.full > .section-text{
    display: none;
}
.section.full > .section-image{
    grid-column: 1/-1;
}
#first{
    background-image: url('../../assets/background/first.jpg');
}
